/*Blocks*/

.block {
    padding: 15px;
    margin-bottom: 15px;
    h3 {
        margin-top: 0;
        padding-top: 0;
        }
    iframe{
        width: 100%;
    }
}

.block-primary {
    @extend .block;
    @include add-background($primary-color);
    
}

.block-secondary {
    @extend .block;
    @include add-background($secondary-color);
    
}

.block-light {
    @extend .block;
    @include add-background($light-color);
    
}

.block-dark {
    @extend .block;
    @include add-background($dark-color);
    
}
.block-center {
    text-align: center !important;
}
.block-image img{
    width: 100%;
    
}
.block-border{
    @include add-border (1px, #cccccc, all);
}

.block-primary-head h3 {
        padding:15px 5px 15px 10px;
        @include add-background($primary-color);
        margin: 0;
        font-size: 18px;
    }
.block-primary-head .block-content {
        @include add-border(1px, $primary-color, all);
        padding: 10px;
    } 
.list-img{
    margin-right: 10px;
}
    
.block-icon {
    h3 {
        padding: 0;
        margin: 0;
    }
    .icon {
        float: left;
        width: 20%;
        margin-top: 10px;
    }
    .icon-content{
        float: left;
        width: 70%;
    }
}

